.form-file-upload {
  height: 16rem;
  width: 28rem;
  max-width: 100%;
  text-align: center;
  position: relative;
  .input-file-upload {
    display: none;
  }
  .label-file-upload {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-width: 2px;
    border-radius: 1rem;
    border-style: dashed;
    border-color: currentColor;
    background-color: rgba(183, 183, 183, 0.27);
    &.drag-active {
      background-color: rgba(183, 183, 183, 0.5);
    }
  }
  .drag-file-element {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
  }
  .upload-button:hover {
    text-decoration-line: underline;
  }
}

